import React, { Component } from 'react';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { Carousel } from 'react-responsive-carousel';
import './header.css';


export default class header extends Component {
    render() {
        const rend = this.props.popularMovies.map(t => (
            <div style={{width:'10c0%'}}>
              <img style={{width:'100%'}} src={`https://image.tmdb.org/t/p/original${t.backdrop_path} `} alt={t.title}/>
            </div>
          ))
        return (
            <Carousel className="header" autoPlay={true} infiniteLoop={true} useKeyboardArrows  showThumbs={false} showIndicators={true} showStatus={false} transitionTime='.5s'>
                {rend}
            </Carousel>
            
        )
    }
}
