import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import './Netflex.css';
import SearchInput from './SearchInput';
import logo from './images/menu.png';
import './Navbar.css';


export default class Navbar extends Component {

    constructor(props){
        super(props);
        this.state = {
            isImp : false
        }
    }
    

    render() {
        return (
                   
            <div className="Navbar">
                <div className="logo-img">
                  <NavLink to="/">  <img className="logo" src="https://image.tmdb.org/t/p/original/wwemzKWzjKYJFfCeiB57q3r4Bcm.svg" alt="netflex"/></NavLink>
                  <img  className="logo menu" src={logo} onClick={() => (this.setState({isImp : !this.state.isImp}))}/>
                </div>
                <nav className={`navbar-container`} style= { this.state.isImp === true  ? {display:'flex'  } : {}}  >  
                  
                    <NavLink className="NavLink" exact to="/Project" >Home</NavLink>
                    <NavLink className="NavLink" exact to ="/Project/tvshows" >TvShows</NavLink >
                    <NavLink className="NavLink" exact to="/Project/movies" >Movies</NavLink >
                    <NavLink className="NavLink" exact to="/Project/new"  >Hindi Movies</NavLink >   
                                     
                </nav>
                <div className="search-input">
                    <SearchInput handleSearch={this.props.handleSearch} data={this.props.searched}/>
                </div>
            </div>     
                  
            
        )
    }
}
